import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';

const useStyles = makeStyles((theme) => ({
  root: {
    margin: 0,
    padding: 0,
    // width: 400,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    flexDirection: 'column',
    padding: '10px 0',
    '& .MuiTextField-root': {
      width: '95%',
      marginBottom: "10px"
    },
    '& button': {
      textTransform: "capitalize",
      width: '95%',
    },
  },
}));

export default function ContactForm() {
  const classes = useStyles();

  return (
    <>
      <form className={classes.root} noValidate autoComplete="off">
          <TextField className={classes.input} size="small" label="Nombre" variant="outlined" />
          <TextField className={classes.input} size="small" label="Apellido" variant="outlined" />
          <TextField className={classes.input} size="small" label="Email" variant="outlined" />
          <TextField className={classes.input} size="small" label="Celular" variant="outlined" />
          <TextField
          size="small"
          className={classes.input}
          id="outlined-multiline-static"
          label="Mensaje"
          multiline
          rows={4}
          variant="outlined"
        />
                <Button variant="contained" size="large" color="primary" className={classes.button}>
          Enviar
        </Button>
      </form>
    </>
  );
}